/* src/App.css */
.container {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
}

input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  margin: 10px 0;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

button:disabled {
  background-color: #d3d3d3;
}

.error {
  color: red;
  text-align: center;
}

.medical-data {
  margin-top: 20px;
}

.category {
  margin-bottom: 20px;
}

.category h2 {
  font-size: 18px;
}

.category p {
  font-size: 16px;
}
