.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.form-section {
  margin-bottom: 30px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  text-align: center; /* Center-align the text inside the label */
}


input,
select {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

button {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.categories {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.category-item {
  flex: 1 1 calc(33.33% - 10px);
}


.error {
  color: red;
  font-weight: bold;
  margin-top: 15px;
}
.medical-data .category {
  margin-top: 20px;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  white-space: pre-wrap; /* Preserve spacing and line breaks */
  word-wrap: break-word; /* Break long words to fit within the box */
  overflow-wrap: break-word; /* Ensures unbroken words wrap properly */
  max-height: 300px; /* Limit the height of the container */
  overflow-y: auto; /* Enable vertical scrolling if content overflows */
}

.medical-data .category h2 {
  margin-bottom: 10px;
  font-size: 1.5em;
  color: #333;
}

.medical-data .category pre {
  margin: 0;
  font-family: monospace; /* Use a monospace font for better formatting */
  font-size: 1em;
  line-height: 1.5; /* Add spacing between lines for readability */
  white-space: pre-wrap; /* Break text into new lines if it overflows */
  word-wrap: break-word; /* Break long words into smaller parts */
  overflow-wrap: anywhere; /* Break even unbreakable words at any point */
}